body {
  background: black;
}

* {
  margin: 0;
  padding: 0;
}
.App {
  color: gold;
  height: 100vh;
  width: 80vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .App .background {
    position: absolute;
    z-index: -1; }

.ScrollingText {
  height: 20%;
  text-align: center;
  width: 30ch;
  line-height: 5ch;
  position: relative;
  font-size: 2rem;
  overflow: hidden; }
  .ScrollingText section {
    -webkit-animation: scroll infinite linear 55s;
            animation: scroll infinite linear 55s; }

@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translateY(10%);
            transform: translateY(10%); }
  100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%); } }

@keyframes scroll {
  0% {
    -webkit-transform: translateY(10%);
            transform: translateY(10%); }
  100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%); } }

.FilterSection {
  padding: 1%;
  height: 10%;
  width: 100%;
  display: flex;
  justify-content: space-around; }
  .FilterSection button {
    width: 100px;
    height: 50px;
    border: 1px solid red;
    margin: 1%;
    border-radius: 25px;
    background: #000;
    color: gold;
    text-transform: capitalize; }
    .FilterSection button:focus {
      outline: none; }
    .FilterSection button:hover {
      background: red; }
  .FilterSection .selected {
    outline: none;
    background: gold;
    color: black; }

.CardArea {
  display: flex;
  flex-wrap: wrap;
  height: 70%;
  justify-content: center;
  overflow-y: scroll;
  padding-top: 5%;
  position: relative;
  width: 100%;
  background-image: url("https://goo.gl/images/KkrFxx"); }
  .CardArea .next,
  .CardArea .previous {
    position: absolute;
    top: 0;
    width: 30px;
    height: 30px;
    border-radius: 20px;
    background: rgba(0, 0, 0, 0.5);
    border: red 1px solid;
    color: gold; }
    .CardArea .next:focus,
    .CardArea .previous:focus {
      outline: none; }
    .CardArea .next:hover,
    .CardArea .previous:hover {
      background: red; }
  .CardArea .previous {
    left: 10px; }
  .CardArea .next {
    right: 10px; }

.Card {
  border: gold 1px solid;
  position: relative;
  margin: 1%;
  min-height: 150px;
  max-height: 200px;
  padding-top: 2%;
  text-align: center;
  border-radius: 2px;
  width: 30%;
  display: grid;
  background: rgba(0, 0, 0, 0.5); }
  .Card .favorite-card {
    position: absolute;
    right: 0;
    height: 20px;
    width: 20px;
    border-radius: 20px; }
  .Card:hover {
    background: gold;
    color: black; }

